.socialAccountsBlock {
    max-height: 450px;
    width: 300px;
    background-color: white;
    display: flex;
    flex-direction: column;
    box-shadow: 1px 1px 2px 1px rgba(102, 102, 102, 0.2)
}

.socialLink {
    padding: 25px 0px 20px 20px;
    border-bottom: 3px solid rgb(243, 242, 242);
    display: flex;
    flex-direction: row;
    height: 40px;
}

.socialLink a{
    text-decoration: none;
    color: #0066cc;
}

.socialLink img{
    height: 16px;
    padding-top: 4px;
    padding-right: 10px;
}

.socialLinkTop {
    padding: 5px 20px;
    height: 45px;
    border-bottom: 3px solid rgb(243, 242, 242);
    display: flex;
    align-items: center
}

