.newsBlock {
    max-height: 330px;
    width: 300px;
    background-color: white;
    display: flex;
    flex-direction: column;
    box-shadow: 1px 1px 2px 1px rgba(102, 102, 102, 0.2);
}

.newsLink img {
    height: 18px;
}

.newsLink a{
    text-decoration: none;
    color: #0066cc;
}

.newsLink {
    padding: 10px 0px 10px 20px;
    border-bottom: 3px solid rgb(243, 242, 242);
    display: flex;
    flex-direction: row;
    height: 70px;
    flex-wrap: wrap;
    align-items: center
}

.newsLink img, p{
    margin: 0;
    padding-right: 10px;
}

.newsLinkTop {
    padding: 5px 20px;
    height: 45px;
    border-bottom: 3px solid rgb(243, 242, 242);
    display: flex;
    align-items: center
}
