#navbar ul {
    display: none;
    background-color: #f90;
    position: absolute;
    top: 100%;
    padding: 0px 220px;
}

#navbar li:hover ul {
    display: block;
}

#navbar, #navbar ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

#navbar {
    border-bottom: 1px solid rgba(160, 160, 160, 0.3);
    height: 40px;
    padding-left: 25px;
    min-width: 470px;
}

#navbar li {
    float: left;
    position: relative;
    height: 100%;
    font-weight: 500;
}

#navbar li a {
    display: block;
    padding: 6px;
    width: 100px;
    color: rgb(0, 0, 0);
    text-decoration: none;
    text-align: center;
}

#navbar ul li {
    float: none;
}

#navbar li:hover {
    border: 1px solid rgb(160, 160, 160, 0.3);
    border-bottom: 1px solid rgb(243, 242, 242);
    margin:0;
    padding:0;
    font-weight: 900;
}

#navbar ul li:hover {
    background-color: #666;
}

#navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 70px 240px 0px 240px;
    margin-top: 0px;
    background-color:rgb(243, 242, 242);
}