.FullMainBar {
    display: flex;
    justify-content: flex-start;
}

.ProfileItems{
    display: flex;
    flex-direction: column;
    margin: 0px 0px 0px 15px;
    padding: 0;
}

.item2 {
    margin: 0;
}

.item3 {
    margin: 0;
}

.PhotoMain {
    position: relative;
    background-color: rgb(255, 255, 255);
    width: 180px;
    height: 180px;
    border-radius: 50%;
    z-index: 1;
    top: -80px;
    margin: 0;
    padding: 1px;
    box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.2);
}

.MainProfilePhoto {
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 1px 1px;
    max-width: 178px;
    max-height: 178px;
    border-radius: 50%;
    object-fit: cover;
}

.item1 {
    font-size: 24px;
    margin: 0px 0px 15px 0px;
}