.ProfileEventBar {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 350px;
}

.ProfileEventButton {
    border-radius: 4%;
    background-color: rgb(236, 172, 53);
    border: none;
    color: rgb(49, 49, 49);
    padding: 12px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    -webkit-transition-duration: 0.4s;
    transition-duration: 0.4s;
}
  
.ProfileEventButton {
    box-shadow: 0 2px 5px 0 rgba(255, 255, 255, 0.2), 0 6px 20px 0 rgba(255, 255, 255, 0.19);
}
  
.ProfileEventButton:hover {
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.24),0 7px 25px 0 rgba(0, 0, 0, 0.24);
}

.EventCounterBar {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 30px;
}

.DiscussionCounterBar {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.EventDiscussionFullBar {
    display: flex;
    flex-direction: row;
}

.EventCounterBar p{
    display: flex;
    margin: 3px 0px;   
}

.DiscussionCounterBar p{
    display: flex;
    margin: 3px 0px;  
}

.DiscussionCounterBar p b{
    font-size: 32px;
}

