.HeaderLogoMain {
    width: 50px;
    height: 50px;
    margin: 12px;
    
}

.HeaderLogoRow {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 24px;
}

.HeaderLogoText {
    color: white;
}

.HeaderMainBar {
    margin: 0px 20px 0px 40px;
    list-style: none;
    display: flex;
    flex-direction: row;
}

.HeaderMainBar a {
    margin: 20px;
}

.AddEventButton {
    border-radius: 4%;
    background-color: rgb(236, 172, 53);
    border: none;
    color: rgb(49, 49, 49);
    padding: 12px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    -webkit-transition-duration: 0.4s;
    transition-duration: 0.4s;
}
  
.AddEventButton {
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
}
  
.AddEventButton:hover {
    box-shadow: 0 2px 5px 0 rgba(255, 255, 255, 0.24),0 7px 25px 0 rgba(255, 255, 255, 0.24);
}

.IconBarImages {
    margin: 20px;
}

.HeaderIconBar {
    display: flex;
    flex-direction: row;
    align-items: center;
    filter: invert(100%); 
    -webkit-filter: invert(100%);
    opacity: 35%;
}

.profileImg {
    filter: invert(100%); 
    -webkit-filter: invert(100%);
}

.profileFull {
    display: flex;
    flex-direction: row;
    align-items: center;

}

.HeaderFullRow {
    padding: 0px 220px;
    height: 80px;
}